.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(34,34,34,.6);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  transform: scale(0);
  z-index: 9999999;

  &.active {
    transform: scale(1);
  }
}

.modal .modal_window {
  transform: scale(0);
  transition: all 0.5s ease-in-out;
}

.modal.active .modal_window {
  transform: scale(1);
  transition: all 0.5s ease-in-out;
}

.modal_window {
  position: relative;
}

.modal_close {
  position: absolute;
  top: 15px;
  right: 15px;

  &:hover {
    cursor: pointer;
    fill: #4A50ED;
  }
}

@media(max-width: 767px) {
  .modal {
    padding-right: 15px;
    padding-left: 15px;
  }

  .modal_close {
    width: 24px;
    height: 24px;
    top: 12px;
    right: 12px;
  }
}
