.header {
  background: #FFFFFF;
  box-shadow: 0px 6px 24px rgba(37, 110, 221, 0.12);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;

  & .center {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

.header_contacts {
  display: flex;
  flex-direction: row;
}

.header_link {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 90%;
  color: #4A50ED;
  text-decoration: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 30px;

  &:hover {
    cursor: pointer;
    color: #242424;

    & svg {
      fill: #242424;

      & path.change {
        stroke: #242424;
      }
    }
  }
}

.header_img {
  margin-right: 4px;
}

.header_button {
  border: 1px solid #4A50ED;
  box-sizing: border-box;
  border-radius: 6px;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 90%;
  color: #3A3A3A;
  min-width: 200px;
  padding-top: 18px;
  padding-bottom: 18px;
  background: white;

  &:hover {
    cursor: pointer;
    background: #4A50ED;
    color: white;
  }
}

@media(max-width: 767px) {
  .header .center {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .header_logo {
    width: 110px;
    height: 30px;
  }

  .header_link {
    display: none;
  }

  .header_button {
    font-size: 14px;
    min-width: 127px;
    padding: 13px 0;
  }
}
