.feedback {
  background: #4A50ED;
  padding: 120px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.feedback_form {
  background: white;
  border-radius: 6px;
  padding: 50px 85px;
  display: flex;
  flex-direction: column;
  max-width: 570px;
  width: 100%;
  box-sizing: border-box;
}

.feedback_title {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 100%;
  text-align: center;
  color: #11244B;
  margin-bottom: 18px;
}

.feedback_subtitle {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120%;
  text-align: center;
  color: #5D6387;
  margin-bottom: 19px;
}

.feedback_prompt {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 140%;
  color: #11244B;
  margin-top: 21px;
  margin-bottom: 5px;
}

.feedback_text-input {
  background: #F8F8F8;
  border: 1px solid #D9D9D9;
  border-radius: 4px;
  padding: 18px 20px;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 19px;
  line-height: 120%;
  color: black;
  appearance: none;

  &:hover,
  &:focus {
    border-color: #242424;
  }

  &::placeholder {
    font-family: 'Ubuntu', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 19px;
    line-height: 120%;
    color: #B9B9B9;
  }

  &.valid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.646 14.818a.5.5 0 00.707 0l8.132-8.132A1 1 0 1119.9 8.1l-9.193 9.193a1 1 0 01-1.414 0l-4.95-4.95a1 1 0 111.414-1.414l3.89 3.89z' fill='%2315AF40'/%3E%3C/svg%3E") 98% center no-repeat,  #F8F8F8;
  }

  &.invalid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17 7L7 17' stroke='%23DC5858' stroke-width='2' stroke-linecap='round'/%3E%3Cpath d='M17 17L7 7' stroke='%23DC5858' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E%0A") 98% center no-repeat,  #F8F8F8;
  }
}

.feedback_button {
  background: #4A50ED;
  border-radius: 6px;
  margin-top: 25px;
  padding: 22px 0;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 90%;
  color: #FFFFFF;
  border: 1px solid #4A50ED;

  &:hover {
    cursor: pointer;
    background: white;
    color: #3A3A3A;
  }

  &:disabled {
    opacity: 0.3;

    &:hover {
      cursor: default;
      background: #4A50ED;
      color: #FFFFFF;
    }
  }
}

@media(max-width: 767px) {
  .feedback {
    padding: 80px 15px;
  }

  .feedback_form {
    max-width: none;
    padding: 35px 15px;
  }

  .feedback_title {
    font-size: 28px;
    line-height: 110%;
    margin-bottom: 18px;
  }

  .feedback_subtitle {
    font-size: 18px;
    line-height: 120%;
    margin-bottom: 4px;
  }

  .feedback_prompt {
    font-size: 14px;
    line-height: 140%;
    margin-top: 20px;
    margin-bottom: 3px;
  }

  .feedback_button {
    margin-top: 20px;
  }

  .modal .feedback_form {
    padding: 40px 15px;

    & .feedback_title {
      font-size: 28px;
      line-height: 110%;
      margin-bottom: 14px;
    }

  }
}