.quiz {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: url('../img/background.svg') no-repeat;
  background-size: cover;
  padding-top: calc(82px + 30px);
  padding-bottom: 30px;
}

.question {
  background: #FFFFFF;
  box-shadow: 0px 10px 80px -30px rgba(74, 80, 237, 0.3);
  border-radius: 6px;
  max-width: 880px;
  width: calc(100% - 30px);
  min-height: 500px;
  display: none;
  flex-direction: column;
  margin-right: 15px;
  margin-left: 15px;

  &.active {
    display: flex;
  }

  &.resoult {
    padding: 71px 50px 75px 50px;
    box-sizing: border-box;

    & form {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
    }
  }
}

.question_header {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 30px;
  line-height: 100%;
  color: #11244B;
  padding: 30px 60px;
  border-bottom: 1px solid #D0D0D0;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
}

.question_body {
  padding: 30px 60px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
}

.question_title {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 21px;
  line-height: 100%;
  color: #11244B;
  width: 100%;
}

.question_radio,
.multipleChoiceOrAll input[type="checkbox"] {
  display: none;
}

.question_answer {
  padding: 14px 20px;
  border: 1px solid #D0D0D0;
  box-sizing: border-box;
  border-radius: 6px;
  position: relative;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-basis: 46.1%;
  font-size: 16px;

  &:nth-of-type(even) {
    margin-left: 20px;
  }

  &::before {
    content: "";
    display: inline-block;
    width: 18px;
    height: 18px;
    border: 1px solid #D0D0D0;
    border-radius: 50%;
    margin-right: 15px;
  }

  &:hover {
    cursor: pointer;
    border-color: #4A50ED;
  }
}

.question_radio:checked + .question_answer,
.multipleChoiceOrAll input[type="checkbox"]:checked + .question_answer {
  border-color: #4A50ED;

  &::before {
    border-color: #4A50ED;
    background: url("data:image/svg+xml,%0A%3Csvg width='10' height='10' viewBox='0 0 10 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='5' cy='5' r='5' fill='%234A50ED'/%3E%3C/svg%3E") center no-repeat;
  }
}

.question_footer {
  display: flex;
  flex-direction: row;
  border-top: 1px solid #D0D0D0;
  margin-top: auto;
  padding: 30px 60px;
}

.question_steps-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 110%;
  color: #7B7B7B;
  margin-right: 60px;
  flex-basis: 55.9%;

  &.mobile {
    display: none;
  }
}

.question_steps {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 110%;
  color: #11244B;
}

.question_scale {
  background: #E0E0E0;
  border-radius: 25px;
  display: block;
  width: 100%;
  height: 8px;
  position: relative;
  margin-bottom: 8px;
  margin-top: 12px;
}

.question_slider {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 50%;
  height: 8px;
  background: #4A50ED;
  border-radius: 25px;
}

.question_paginator {
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-basis: 275px;
  flex-shrink: 0;
}

.question_next {
  background: #4A50ED;
  border-radius: 6px;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  color: #FFFFFF;
  border: 1px solid #4A50ED;
  box-sizing: border-box;
  padding: 17px 0;
  flex-grow: 1;
  max-height: 55px;
  height: 100%;
  flex-basis: 200px;
  flex-shrink: 0;

  &:hover {
    cursor: pointer;
    background: white;
    color: #4A50ED;

    & path {
      stroke: #4A50ED;
    }
  }

  &:disabled {
    opacity: 0.5;

    &:hover {
      cursor: auto;
      background: #4A50ED;
      color: white;

      & path {
        stroke: white;
      }
    }
  }
}

.question_prev {
  background: #F8F8F8;
  border: 1px solid #D9D9D9;
  box-sizing: border-box;
  border-radius: 6px;
  flex-basis: 55px;
  padding: 0;
  max-height: 55px;
  height: 100%;
  flex-shrink: 0;
  margin-right: 20px;

  &:hover {
    cursor: pointer;
  }
}

.question_text {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 140%;
  color: #B9B9B9;
  margin-bottom: 10px;
}

.question_subtitle {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 29px;
  line-height: 100%;
  color: #11244B;
  margin-bottom: 35px;
}

.question_text-input {
  background: #F8F8F8;
  border: 1px solid #D9D9D9;
  box-sizing: border-box;
  border-radius: 4px;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  color: black;
  padding: 17px 20px;
  margin-bottom: 10px;
  max-width: 400px;
  width: 100%;
  appearance: none;

  &::placeholder {
    font-family: 'Ubuntu', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    color: #B9B9B9;
  }

  &.valid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.646 14.818a.5.5 0 00.707 0l8.132-8.132A1 1 0 1119.9 8.1l-9.193 9.193a1 1 0 01-1.414 0l-4.95-4.95a1 1 0 111.414-1.414l3.89 3.89z' fill='%2315AF40'/%3E%3C/svg%3E") 98% center no-repeat,  #F8F8F8;
  }

  &.invalid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17 7L7 17' stroke='%23DC5858' stroke-width='2' stroke-linecap='round'/%3E%3Cpath d='M17 17L7 7' stroke='%23DC5858' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E%0A") 98% center no-repeat,  #F8F8F8;
  }
}

.question_submit {
  background: #4A50ED;
  border-radius: 6px;
  border: 1px solid #4A50ED;
  margin-top: 20px;
  margin-bottom: 37px;
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  color: #FFFFFF;
  padding: 22px;
  max-width: 280px;
  width: 100%;

  &:hover {
    cursor: pointer;
    background: white;
    color: #4A50ED;

    & path {
      stroke: #4A50ED;
    }
  }

  &:disabled {
    opacity: 0.5;

    &:hover {
      cursor: auto;
      background: #4A50ED;
      color: white;

      & path {
        stroke: white;
      }
    }
  }
}

#accepted {
  display: none;
}

.question_accepted {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 15px;
  color: #7A7A7A;
  padding-left: 39px;
  position: relative;

  & a {
    color: #7A7A7A;
    margin-left: 4px;
  }

  &::before {
    content: "";
    display: inline-block;
    width: 24px;
    height: 24px;
    border: 1px solid #B9B9B9;
    position: absolute;
    top: calc(50% - 12px);
    left: 0;
  }

  &:hover {
    cursor: pointer;
  }
}

#accepted:checked + .question_accepted::before {
  background: url("data:image/svg+xml,%0A%3Csvg width='16' height='12' viewBox='0 0 16 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 4.45363L6.54493 10L15 1' stroke='%234A50ED' stroke-width='2'/%3E%3C/svg%3E%0A") center no-repeat;
}

.question_prompt {
  display: none;
}

@media(max-width: 767px) {
  .quiz {
    padding-top: calc(60px + 30px);
    padding-bottom: 30px;
  }

  .question.resoult {
    padding: 35px 15px;
  }

  .question_header {
    padding: 20px 15px;
  }

  .question_body {
    padding: 20px 10px;
    flex-direction: column;
  }

  .question_title {
    font-size: 19px;
    line-height: 115%;
    margin-bottom: 4px;
  }

  .question_answer {
    padding: 14px 14px;
    margin-top: 10px;
    flex-basis: auto;
    align-self: stretch;
  
    &:nth-of-type(even) {
      margin-left: 0;
    }
  }

  .question_footer {
    border: none;
    padding: 0 15px 20px 15px;
  }

  .question_steps-wrapper {
    font-size: 14px;
    margin-right: 0;
    flex-basis: auto;
    padding: 22px 15px 0 15px;
  
    &.mobile {
      display: flex;
    }

    &.desctop {
      display: none;
    }
  }

  .question_paginator {
    flex-basis: auto;
  }

  .question_next {
    padding: 14px 0;
    max-height: 50px;
    height: 100%;
    flex-basis: 195px;
    flex-shrink: 0;
  }
  
  .question_prev {
    flex-basis: 50px;
    max-height: 50px;
    margin-right: 15px;
  }

  .question_prompt {
    display: block;
    font-family: 'Ubuntu', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 13px;
    line-height: 110%;
    color: #11244B;
    margin-top: 6px;
    margin-bottom: 4px;
  }

  .question_text {
    font-size: 16px;
    margin-bottom: 6px;
  }
  
  .question_subtitle {
    font-size: 24px;
    line-height: 115%;
    margin-bottom: 14px;
  }
  
  .question_text-input {
    margin-bottom: 10px;
    max-width: none;
    width: 100%;
  }
  
  .question_submit {
    margin-top: 24px;
    margin-bottom:16px;
    padding: 20px;
    max-width: none;
    width: 100%;
  }
}